import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import Styled from 'styled-components';
import Theme from '../common/NekoTheme';

function sizeToStr(value) {
  if (typeof value === 'number') return `${value}px`;
  return value;
}

/**
 * @param {string} id
 * @param {string} name
 * @param {bool} checked
 * @param {string} label
 * @param {string} description
 */
const StyledNekoSwitch = Styled(props => {
  const { onValue, offValue, checked = false, onChange, disabled, ...rest } = props;

  const onToggle = useCallback((v) => {
    const on = onValue !== undefined ? onValue : true;
    const off = offValue !== undefined ? offValue : false;
    onChange(v.target.checked ? on : off);
  }, [onValue, offValue, onChange]);

  return (
    <label className="neko-switch" {...rest} data-is-disabled={disabled ? 'disabled' : ''}>
      <input type="checkbox" onChange={onToggle} checked={checked} disabled={disabled} />
      <span className="neko-switch-slider"></span>
    </label>
  );
})`
  color: ${Theme.white};
  font-family: ${Theme.fontFamily};
  font-size: ${props => props.fontSize};
  position: relative;
  display: inline-block;
  width: ${props => sizeToStr(sizeToStr(props.width))};
  height: ${props => sizeToStr(props.height)};

  &[data-is-disabled=disabled] {
    opacity: 0.4;

    .neko-switch-slider {
      cursor: auto;
    }
  }

  input {
    opacity: 0;
    width: 0;
    height: 0;
    border: 0;
  }

  .neko-switch-slider {
    background-color: ${props => props.offBackgroundColor};
    border-radius: 35px;
    align-items: center;
    cursor: pointer;
    display: inline-flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: .4s;
  }

  .neko-switch-slider:before {
    border-radius: 50%;
    position: absolute;
    content: "";
    height: ${props => `calc(${sizeToStr(props.height)} - 8px)`};
    width: ${props => `calc(${sizeToStr(props.height)} - 8px)`};
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .3s;
  }

  .neko-switch-slider:after {
    content: "${props => props.offLabel}";
    margin-left: auto;
    margin-right: ${props => `calc(${sizeToStr(props.height)} / 2)`};
  }

  input:checked + .neko-switch-slider {
    background-color: ${props => props.onBackgroundColor};
  }

  input:checked + .neko-switch-slider:before {
    transform: translateX(${props => `calc(${sizeToStr(props.width)} - ${sizeToStr(props.height)})` });
  }

  input:checked + .neko-switch-slider:after {
    content: "${props => props.onLabel}";
    margin-left: ${props => `calc(${sizeToStr(props.height)} / 2)`};
    margin-right: auto;
  }
`;

const NekoSwitch = (props) => {
  return (<StyledNekoSwitch {...props} />);
};

NekoSwitch.propTypes = {
  width: PropTypes.number,
  height: PropTypes.number,
  fontSize : PropTypes.string,
  onValue: PropTypes.string,
  offValue: PropTypes.string,
  checked: PropTypes.bool,
  onBackgroundColor: PropTypes.string,
  offBackgroundColor: PropTypes.string,
  onLabel: PropTypes.string,
  offLabel: PropTypes.string
};

NekoSwitch.defaultProps = {
  width: 75,
  height: 30,
  fontSize: Theme.fontSizeSmall,
  onLabel: 'Yes',
  offLabel: 'No',
  onBackgroundColor: Theme.primary,
  offBackgroundColor: Theme.gray,
  checked: false
};

export { NekoSwitch };
